
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import Search from 'vux/src/components/search/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import serviceGetData from '../../service/getData.js'
import dateFormat from 'vux/src/tools/date/format.js'

import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import XDialog from 'vux/src/components/x-dialog/index.vue'

import service_upData from '../../service/upData.js'

export default {
  directives: {
      TransferDom
  },
  components: {
    Search, Scroller, Divider, Spinner,
    XHeader, Checker, CheckerItem, Confirm, XDialog, Toast
  },
  created(){

    // 查询数据要求
      // page | int | 页数
      // length | int | 每页记录数
      // createTimeStart | Long | 申请开始时间
      // createTimeEnd | Long | 申请结束时间
      // status | int | 1 代表处理中，2代表已完成
      // type | int | 1 代表常规染色 ，2代表 冰冻预约 ，3代表染色申请，什么都不传是全部
      // filter | String | 病理号
      // sort | int | 1 代表升序，2代表降序


    serviceGetData.info_list({
      createTimeEnd:(+new Date()),
      createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 默认获取最近一个月数据
      type:(this.user_type==4 ? '' : this.user_type), // 默认状态 处理中
      status:this.user_status, // 1 代表处理中，2代表已完成
      filter:this.filter, // 病理号
      page:this.user_page, // 数据页数
      sort:this.user_sort, // 1 代表升序，2代表降序
      length:this.user_length, // 每页记录数

    }).then(res=>{
      this.info_list = res
      this.$store.state.info_list = res
    },res=>console.log(res))


  },
  filters:{
    dateFormat(par,form){
      return dateFormat(new Date(par), form)
    }
  },
  data () {
    return {
      show_focus_search_fn:true,
      // n: 10,
      pullupEnabled: true,
      status: {
        pullupStatus: 'default',
        pulldownStatus: 'default'
      },
      pulldown_config: {
        content: 'content',
        pullUpHeight: 60,
        height: 40,
        autoRefresh: false,
        downContent: '下拉刷新',
        upContent: '松开开始刷新',
        loadingContent: '正在刷新...',
        clsPrefix: 'clsPrefix'
      },
      no_moer:false, // 没有更多
      value: '',

      user_time:1, // 默认时间 最近一个月
      user_type:'', // 1 代表常规染色 ，2代表 冰冻预约 ，3代表染色申请，什么都不传是全部
      user_status:'', // 1 代表处理中，2代表已完成，什么都不传是全部
      user_filter:'', // 病理号
      user_page:1, // 数据页数
      user_sort:2, // 1 代表升序，2代表降序
      user_length:10, // 每页记录数

      // user_page_load:1, // 分布器自增的页码
      filter_status:null,
      filter_type:null,
      toast_load:false,
      results: [], // 搜索条搜索结果
      data_index:-1,// 当前点击的数组位置
      info_list:{}, // 申请信息列表
      confirm_show:false, // 取消预约提示
      showContent:false, // 筛选
      cancel_pre_id:'', // 取消预约
    }
  },
  computed:{
    to_user_time(){
      var mydate = new Date();
      return {one_month:mydate-2592000000, three_months:mydate-7776000000, a_year:mydate-31536000000}
    }
  },
  methods:{
    focus_search_fn(){
      console.log('jd')
    },
    cancel_search_fn(){
      console.log('取消搜索')
      this.show_focus_search_fn=true

      if(this.user_time==''){
        alert('必须选择一个时间')
        return
      }

      // 重置为第一页并开启上拉功能
        this.user_page=1
        this.$refs.scroller.donePulldown()
        this.$refs.scroller.enablePullup()
        this.pullupEnabled = true

      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 默认获取最近一个月数据
        type:(this.user_type==4 ? '' : this.user_type), // 默认状态 处理中
        status:this.user_status, // 1 代表处理中，2代表已完成
        filter:this.filter, // 病理号
        page:this.user_page, // 数据页数
        sort:this.user_sort, // 1 代表升序，2代表降序
        length:this.user_length, // 每页记录数

      }).then(res=>{
        this.info_list = res
        this.$store.state.info_list = res
      },res=>console.log(res))



    },
    loadMore () {
      console.log(this.info_list.data.length , this.info_list.total)
      if( this.info_list.data.length >= this.info_list.total ){
        // alert('没有更多了')
        console.log('没有更多了')
        this.$refs.scroller.disablePullup()
        this.$refs.scroller.donePulldown()
        this.no_moer=true

        // 请求完成时执行下面语句

      }else{
        this.no_moer=false

        // 新

          setTimeout(() => {
            this.user_page ++
            setTimeout(() => {

              serviceGetData.info_list({
                createTimeEnd:(+new Date()),
                createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 默认获取最近一个月数据
                type:(this.user_type==4 ? '' : this.user_type), // 默认状态 处理中
                status:this.user_status, // 1 代表处理中，2代表已完成
                filter:this.filter, // 病理号
                page:this.user_page, // 数据页数
                sort:this.user_sort, // 1 代表升序，2代表降序
                length:this.user_length, // 每页记录数

              }).then(res=>{
                this.info_list.data = this.info_list.data.concat(res.data)
                this.$store.state.info_list.data = this.$store.state.info_list.data.concat(res.data)

                this.$refs.scroller.donePullup()

              },res=>console.log(res))

            }, 10)
          }, 300) // 要设计一定的延迟，否则会导致页数连加两次

      }

      // 原
        // setTimeout(() => {
        //   this.n += 10
        //   setTimeout(() => {
        //     this.$refs.scroller.donePullup()
        //   }, 10)
        // }, 2000)
    },

    refresh () {
      // 新代码
        this.$nextTick(() => {
          this.no_moer=false
          // 请求完成时执行下面语句
          this.$refs.scroller.donePulldown()
          this.$refs.scroller.enablePullup()
          this.pullupEnabled = true
        })


      // 原
        // setTimeout(() => {
        //   this.n = 10
        //   this.$nextTick(() => {
        //     setTimeout(() => {
        //       this.$refs.scroller.donePulldown()
        //       this.pullupEnabled && this.$refs.scroller.enablePullup()
        //     }, 10)
        //   })
        // }, 2000)
    },
    changePullupStatus (enabled) {
      if (enabled) {
        this.$refs.scroller.enablePullup()
        this.pullupEnabled = true
      } else {
        this.$refs.scroller.disablePullup()
        this.pullupEnabled = false
      }
    },
    cancel_pre_ok(){
      service_upData.cancel_pre(this.cancel_pre_id).then(res=>{
        this.toast_text='已取消'
        this.toast_load=true
        this.info_list.data.splice(this.data_index,1); // 删除本地数据
      },res=>{
        this.toast_text='失败'
        this.toast_load=true
      })
    },
    click_item(index, aid_aiId, text, type){
      this.$router.push({name:'details',query:{index:index, aid_aiId:aid_aiId, text:text, type:type}})
      console.log(index, aid_aiId, type)
    },
    click_pre(index,id){
      this.data_index = index // 当前点击的数据位置
      this.cancel_pre_id=id
      this.confirm_show=true
    },

    resultClick(item){
      console.log(item)
      this.show_focus_search_fn=false
      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(new Date() - 31536000000), // 默认获取一年前数据,
        // status:this.user_status,
        // type:this.user_type == 4 ? '' : this.user_type ,
        sort:this.user_sort,
        filter:item.myval,
      }).then(res=>{
          this.info_list = res
          this.$store.state.info_list = res
      },res=>console.log(res))

      // this.info_list.data=[]
      // alert( JSON.stringify(item) )
    },
    getResult (val) {
      this.results = val ? getResult(this.value) : []
    },
    switch_sort(){

      var createTimeEnd=(+new Date());
      var createTimeStart=(new Date() - 31536000000); // 默认获取一年前数据

      this.user_sort = this.user_sort==1 ? 2 : 1;

      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 默认获取最近一个月数据
        type:(this.user_type==4 ? '' : this.user_type), // 默认状态 处理中
        status:this.user_status, // 1 代表处理中，2代表已完成
        filter:this.filter, // 病理号
        page:this.user_page, // 数据页数
        sort:this.user_sort, // 1 代表升序，2代表降序
        length:this.user_length, // 每页记录数

      }).then(res=>{
        this.info_list = res
        this.$store.state.info_list = res
      },res=>console.log(res))


    },
    reset(){
      this.user_time=1; // 默认时间 最近一个月
      this.user_type=''; // 默认状态 处理中
      this.user_status=''; // 1 代表处理中，2代表已完成
      this.user_filter=''; // 病理号
      this.user_page=1; // 数据页数
      this.user_sort=2; // 1 代表升序，2代表降序
      this.user_length=10; // 每页记录数
    },
    confirm(){
      if(this.user_time==''){
        alert('必须选择一个时间')
        return
      }
      this.showContent=!this.showContent

      // 重置为第一页并开启上拉功能
        this.user_page=1
        this.$refs.scroller.donePulldown()
        this.$refs.scroller.enablePullup()
        this.pullupEnabled = true

      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 默认获取最近一个月数据
        type:(this.user_type==4 ? '' : this.user_type), // 默认状态 处理中
        status:this.user_status, // 1 代表处理中，2代表已完成
        filter:this.filter, // 病理号
        page:this.user_page, // 数据页数
        sort:this.user_sort, // 1 代表升序，2代表降序
        length:this.user_length, // 每页记录数

      }).then(res=>{
        this.info_list = res
        this.$store.state.info_list = res
      },res=>console.log(res))


    },
  }
}



function getResult (val) {
  $('.weui-cells.vux-search_show').show()
  // .prepend(`
  //     <div class="getResult_xw"></div>

  //   `);
  let rs = [{
    title: `点击查询`,
    myval: val
  }]
  // for (let i = 0; i < 20; i++) {
  //   rs.push({
  //     title: `${val} result: ${i + 1} `,
  //     other: i
  //   })
  // }
  return rs
}










